import { useEffect, useState, useContext } from 'react';
import TimerContainer from './component/timer-container/timer-container.component';
import ScheduleContainer from './component/schedule-container/schedule-container.component';
import CompleteTime from './component/complete-time/complete-time.component';

import { DataContext } from './context/fetchData.context';
import styled from 'styled-components';
import { Button } from 'antd';

const AlertTable = styled.div`
	top: 0;
	left: 0;
	position: fixed;
	z-index: 999;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.2);
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	gap: 20px;
`;
const ContainerStyle = styled.div`
	width: 90vw;
	margin: 0 auto;
`;

function App() {
	const [shouldRefresh, setShouldRefresh] = useState(false);
	const [startTime, setStartTime] = useState(Date.now());
	const { data, reload } = useContext(DataContext);

	useEffect(() => {
		const checkTime = () => {
			const currentTime = new Date();
			const elapsedMinutes = (Date.now() - startTime) / 60000; // ()

			// 12
			const isMidnight = currentTime.getHours() === 0 && currentTime.getMinutes() === 0;

			//  15 
			const isOver15Minutes = elapsedMinutes >= 15;

			if (isMidnight || isOver15Minutes) {
				setShouldRefresh(true);
			}
		};

		// 
		const interval = setInterval(checkTime, 60000);

		return () => clearInterval(interval);
	}, [startTime]);

	// 
	const handleRefresh = () => {
		window.location.reload();
	};
	// ** 15 **
	const handleReload = async () => {
		await reload(); //  API 
		setStartTime(Date.now()); //  15 
		setShouldRefresh(false); // 
	};

	return (
		<ContainerStyle>
			<p>{new Date(startTime).toLocaleTimeString()}</p>
			{shouldRefresh && (
				<AlertTable>
					<p></p>
					<Button
						onClick={handleRefresh}
						size="large"
					>
						
					</Button>
				</AlertTable>
			)}
			<h1></h1>
			<h3></h3>
			<div
				style={{
					display: 'flex',
					width: '90%',
					gap: '20px',
					margin: '0 auto',
					marginBottom: '30px',
				}}
			>
				<TimerContainer />
				<button onClick={handleReload}></button>
			</div>
			<ScheduleContainer />
			<CompleteTime />
		</ContainerStyle>
	);
}

export default App;
